<template>
    <el-dialog
      title=""
      v-model="dialogVisible"
      width="400px"
      align-center
    >
      <p></p>
      <el-checkbox v-model="noMorePrompt"></el-checkbox>
      <template #footer>
        <el-button @click="handleClose"></el-button>
        <el-button type="primary" @click="goToSetting"></el-button>
      </template>
    </el-dialog>
  </template>
  
  <script>
  export default {
    props: {
      modelValue: Boolean,
    },
    emits: ["update:modelValue"],
    data() {
      return {
        noMorePrompt: false,
      };
    },
    computed: {
      dialogVisible: {
        get() {
          return this.modelValue;
        },
        set(val) {
          this.$emit("update:modelValue", val);
        },
      },
    },
    methods: {
      handleClose() {
        if (this.noMorePrompt) {
          localStorage.setItem("noPasswordPromptDisabled", "true");
        }
        this.dialogVisible = false;
      },
      goToSetting() {
        if (this.noMorePrompt) {
          localStorage.setItem("noPasswordPromptDisabled", "true");
        }
        this.dialogVisible = false;
        this.$router.push({ name: "setting" });
      },
    },
  };
  </script>
  